.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  padding: 1rem;
  background: #ffffff;
  border-top: 1px solid #eaeaea;
  user-select: none;
  color: #555;
}

.footer-left,
.footer-right {
  flex: 1;
}

.footer a {
  margin: 0 1rem;
}
